import React, {Component} from 'react';
import './App.css';

import ImageLabel from './ImageLabel/ImageLabel.js';
import Title from './Title/Title.js';

class App extends Component {

    constructor(props) {
        super(props);

        let w = Math.max(document.documentElement.clientWidth, window.innerWidth || 0);
        // let h = Math.max(document.documentElement.clientHeight, window.innerHeight || 0);

        App.setBackgroundImage(w);
    }

    render() {
        return (
            <div id="page_wrapper">
                <Title/>
                <ImageLabel/>
            </div>
        );
    }

    static setBackgroundImage(screenWidth) {
        let dim = "1920x1080";  // 1080p

        if (screenWidth > 2560) {
            dim = "3840x2160";  // 4k
        }
        if (screenWidth > 1920) {
            dim = "2560x1440";  // 1440p
        }

        document.body.style.backgroundImage = "url(https://source.unsplash.com/" + dim + "/?+mountain)";
    }
}

export default App;
